import React, { Component } from "react";
import ProductDisplay from "./ProductDisplay";
import SpringBootServer from "../API/SpringBootServer";

import "./App.css";
class App extends Component {
  state = {
    products: [
      { id: 1, productName: "MACOS", price: 123456, qoh: 1 },
      { id: 2, productName: "Windows", price: 3999, qoh: 100 },
      { id: 3, productName: "Linux", price: 1, qoh: 999999 },
      { id: 4, productName: "Android", price: 6, qoh: 50 },
    ],
    productsFromBackend: [],
  };

  constructor() {
    super();
    console.log("constructore");
  }
  componentDidMount() {
    SpringBootServer.get("products").then((res) => {
      console.log(res);
      this.setState({ productsFromBackend: res.data });
    });

    // console.log("didmount");
    // console.log("fetching position");
    // window.navigator.geolocation.getCurrentPosition((position) => {
    //   console.log(position.coords.latitude);
    // }); //asynchronous call. that is fetching and fetched position will be printed first.
    // // and then after the fucntion completes it'll print location

    // console.log("fetched position...");
  }
  componentDidUpdate() {
    console.log("did updated");
  }
  render() {
    console.log("rendering");
    return (
      <div className="App">
        {this.state.productsFromBackend.map((ele) => (
          <ProductDisplay key={ele.id} product={ele} />
        ))}
      </div>
    );
  }
}

export default App;



