import React, { Component } from "react";
import SpringBootServer from "../API/SpringBootServer";
import ReviewsDisplay from "./ReviewsDisplay";

class ProductDisplay extends Component {
  state = {
    reviews: [],
  };
  showReview = (id) => {
    SpringBootServer.get("products/" + id + "/reviews").then((res) => {
      console.log(res);
      this.setState({ reviews: res.data });
    });
  };

  render() {
    return (
      <div className="ui card">
        <div className="content">
          <a className="header">{this.props.product.name}</a>
          <div className="meta">
            <span className="date">{this.props.product.price}</span>
          </div>
        </div>
        <div className="extra content">
          <a>
            <i className="box icon"></i>

            {this.props.product.qoh}
          </a>
          <button
            className="ui button"
            onClick={() => this.showReview(this.props.product.id)}
          >
            Reviews
          </button>
          <div className="extra content">
            {this.state.reviews.map((review) => {
              return <ReviewsDisplay key={review.id} reviews={review} />;
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default ProductDisplay;



